.posts {
    padding: 120px 0;
    &__title {
        color: violet;
    }
    &__items {
        display: flex;
        @media (max-width: 1024px) {
            display: block; 
        }
        .col {
            width: 50%;
            @media (max-width: 1024px) {
                width: 100%; 
            }
        }
    }
    &__item {
        &-one {
            &-image {
                width: 100%;
                min-height: 465px;
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;  
                }
            }
        }
    }
}


